export interface Store<T = unknown> {
  state: T;
  setState: (newState: T) => void;
}

export const createStore = <T = unknown>(initialState: T): Store<T> => {
  const store = {
    state: initialState,
    setState: (newState: T): void => {
      store.state = newState;
    },
  };

  return store;
};

