  /**  */
  kcmc: string;
  /**  */
  kcywmc: string;
  /**  */
  kcdlmc: string;
  /**  */
  zcj: string;
  /**  */
  zcjfs: number;
  /**  */
  ksxzmc: "" | "" | "";
  /**  */
  cjfsmc: "" | "";
  /**  */
  xf: 3;
  /**  */
  zxs: 28;
  /**  */
  xdfsmc: string;
  /**  */
  kkbmmc: string;

  /**  */
  xnxqdm: "202201";
  /**  */
  kcbh: string;
  /**  */
  kcptbh: string;
  /**  */
  ksxzdm: string;
  /**  */
  kcdm: string;
  /**  */
  xdfsdm: string;
  /**  */
  cjdm: string;
  /**  */
  khfsdm: string;

  /**  */
  xsxm: string;
  /**  */
  xsbh: string;
  /**  */
  xsdm: "22442910";

  xsckcj: "0";
  rownum_: 1;
